import React from 'react'
import {BsFolder} from 'react-icons/bs'
import {RiGithubLine} from 'react-icons/ri'
import {RiExternalLinkLine} from 'react-icons/ri'

export default function CardComponent(props) {
  return (
    <>
      <div className='card'>

        <div className='card-header'>

        <div className='card-left'>
        <span><BsFolder className='card-icon'/></span>
        <span className='card-title'>{props.CardBodyTitle}</span>
        </div>

        <div className='card-right'>
        <a
        href= {props.gitlink}
        alt= ''
        target= '_blank'
        rel= 'noreferrer'
        >
        <RiGithubLine className='card-icons'/>
        </a>

        <a
        href= {props.sitelink}
        alt= ''
        target= '_blank'
        rel= 'noreferrer'
        >
        <RiExternalLinkLine className='card-icons'/>
        </a>
        </div>

        </div>

        <div className='card-body'>
          <span className='card-body-text'>{props.CardBodyText}</span>
        </div>
        <div className='card-footer'>
          <span>{props.tech1}</span>
          <span>{props.tech2}</span>
          <span>{props.tech3}</span>
        </div>
      </div>
    </>
  )
}
