import React, { Component } from 'react'
import {RiPlayLine} from 'react-icons/ri'

export default class BlogSimplfied extends Component {
  render() {
    return (
      <>
       <div className='blog-container-simplified'>

        <div className='top-content'>
        <p>
        I think the knowledge we don't share never grows with us through the ages, hence through my side projects, blog posts, social media and other media streams,
        I try to teach and illustrate what I have learned along the way, representing my current understanding of things. And while I am still seeking inspiration and guidance, I also try to help others in thier journey. So feel free to check out my content on the sources below:
        </p>
        </div>

       <br/>
       <div className='bottom-content'>

        <span className='socials-simplified'>
        <a
        href= 'https://hashnode.com/@husseinkizz'
        target = '_blank'
        rel = 'noreferrer'
        className = 'social-link'
        ><RiPlayLine className='social-link-icon'/>Hashnode</a>
        <a
        href= 'https://twitter.com/HusseinKizz'
        target = '_blank'
        rel = 'noreferrer'
        className = 'social-link'
        ><RiPlayLine className='social-link-icon'/>Twitter</a>
        <a
        href= 'https://dev.to/husseinkizz'
        target = '_blank'
        rel = 'noreferrer'
        className = 'social-link'
        ><RiPlayLine className='social-link-icon'/>Dev Community</a>
        <a
        href= 'https://www.producthunt.com/@husseinkizz'
        target = '_blank'
        rel = 'noreferrer'
        className = 'social-link'
        ><RiPlayLine className='social-link-icon'/>Product Hunt</a>

        </span>

        <span className='empty-simplified'>
        <span className='a-simplified'></span>
        <span className='b-simplified'></span>
        <span className='c-simplified'></span>
        </span>

        </div>

        </div>

      </>
    )
  }
}
