import React, { Component } from 'react'
import App from '../App'
import SplashLoader from './SplashLoader'

/* This component is mostly only responsible for displaying the splash screen on load and then load the main app contents, 
It can be temporary dis-rendered in development phase and re-rendered due production. It took me some time to make it work!

But, to disable it temporary, render the <main/> app component instead of <AppContentLoader/> directly in App.js and reverse this on production!
*/

export default class AppContentLoader extends Component {
  constructor() {
    super()
    this.state = {
      isLoading: true
    }
  }

  componentDidMount() {
    setTimeout(() => {
      this.setState({
        isLoading: false
      })
    }, 6000)
    }

  render() {
    return (
      <>
      {
        this.state.isLoading ?
        <SplashLoader /> :
        <App/>
      }
      </>
    )
  }
}
