import React, { Component } from 'react'
import {RiPlayLine} from 'react-icons/ri'

export default class BlogMobile extends Component {
  render() {
    return (
      <>
       <div className='blog-container-mobile'>

          <div className='top-content'>
          <p>
          I think the knowledge we don't share never grows with us through the ages, hence through my side projects, blog posts, social media and other media streams,I try to teach and illustrate what I have learned along the way, representing my current understanding of things. And while I am still seeking inspiration and guidance, I also try to help others in thier journey. So feel free to check out my content on the sources below:
          </p>
          </div>

            <br/>
              <div className='bottom-content'>

              <span className='socials-mobile'>
              <a
              href= 'https://hashnode.com/@husseinkizz'
              target = '_blank'
              rel = 'noreferrer'
              className = 'social-link'
              ><RiPlayLine className='social-link-icon'/>Hashnode</a>
              <a
              href= 'https://twitter.com/HusseinKizz'
              target = '_blank'
              rel = 'noreferrer'
              className = 'social-link'
              ><RiPlayLine className='social-link-icon'/>Twitter</a>
              <a
              href= 'https://dev.to/husseinkizz'
              target = '_blank'
              rel = 'noreferrer'
              className = 'social-link'
              ><RiPlayLine className='social-link-icon'/>Dev Community</a>

            </span>

            <span className='empty-mobile'>
            <span className='a-mobile'></span>
            <span className='b-mobile'></span>
            <span className='c-mobile'></span>
          </span>

          </div>

        </div>

      </>
    )
  }
}
