import React, { Component } from 'react'
import Reveal from 'react-reveal/Reveal';
import {VscFolderActive} from 'react-icons/vsc'
import CardComponent from './CardComponent'

export default class Projects extends Component {
  render() {
    return (
      <>
        <div className='projects-title'>

        <span className='title-wrap'>

        <span>
        <VscFolderActive className='projects-title-icon'/>
      </span>

      <span>My Projects</span>

      </span>

      <span class='liner'></span>

        </div>

        <div className='cards-container'>
        <div className='project-wrapper'>
        <Reveal effect="fadeInDown">
          <CardComponent
            gitlink = 'https://github.com/Hussseinkizz/hussein-s-portifolio'
            sitelink = 'https://husseinkizz.netlify.app'
            CardBodyTitle = 'my portfolio v2'
            CardBodyText = 'This is the 2nd iteration of my portfolio. Built without CSS frameworks like Bootstrap or Tailwind. Yet I used my personal library Tie & Dye CSS for all the cool tricks!'
            tech1 = 'React'
            tech2 = 'Scss'
            tech3 = 'Tie & Dye'
          /></Reveal></div>
        <div className='project-wrapper'>
        <Reveal effect="fadeInDown">
          <CardComponent
            gitlink = 'https://github.com/Hussseinkizz/Dev-Index'
            sitelink = 'https://dev-index.netlify.app'
            CardBodyTitle = 'Dev Index v3'
            CardBodyText = 'This is a cool web app that indexes all developer resources into one easily accessible place. Mostly the free stuff which saves devs some bucks.'
            tech1 = 'React'
            tech2 = 'Scss'
            tech3 = 'Tailwind'
          /></Reveal></div>
        <div className='project-wrapper'>
        <Reveal effect="fadeInDown">
          <CardComponent
            gitlink = '#'
            sitelink = '#'
            CardBodyTitle = 'Multi Teams v1'
            CardBodyText = 'This is a web app for the multi teams local developer community. This community helps local developers work together on profitable projects and earn a living.'
            tech1 = 'React'
            tech2 = 'Tailwind'
            tech3 = 'Firebase'
          /></Reveal></div>
        <div className='project-wrapper'>
        <Reveal effect="fadeInDown">
          <CardComponent
            gitlink = '#'
            sitelink = '#'
            CardBodyTitle = 'School Free v1'
            CardBodyText = 'This is a saas model based school management software. Built to ensure smooth running of schools especially those with large sums of students.'
            tech1 = 'React'
            tech2 = 'Electron'
            tech3 = 'React Native'
          /></Reveal></div>
        </div>
      </>
    )
  }
}
