import React, { Component } from 'react'
import {RiMailCheckLine} from 'react-icons/ri'
import logo from '../assets/logo-line.svg'
import {RiTwitterLine} from 'react-icons/ri'
import {RiGithubLine} from 'react-icons/ri'
import {RiLinkedinBoxLine} from 'react-icons/ri'
import {RiCodepenLine} from 'react-icons/ri'


export default class Contact extends Component {
  render() {
    return (
      <>
        <div className='contact-title'>

        <span className='title-wrap'>
        
        <span><RiMailCheckLine className='contact-title-icon'/></span>

        <span>Let's Talk</span>

        </span>

        <span class='liner'></span>

        </div>

        <div className='contact-content'>
          <span>
            <img
              src = {logo}
              alt = ''
              className = 'contact-logo'
            />
          </span>
          <p className='contact-text'>
          <span class="contact-me-text">Am available for <span class="text-highlight">hire</span>, <span class="text-highlight">Partnership</span>, <span class="text-highlight">open source</span> or we can work on something together say <span class="text-highlight">collaboration</span> or <span class="text-highlight">Pair Program</span> together, or we can share some thoughts in<span class="text-highlight"> Public talks</span>,<span class="text-highlight">conferences</span>, <span class="text-highlight">Interviews</span>,<span class="text-highlight"> hackthons</span>and<span class="text-highlight">Meetups.</span>So feel free to connect with me on the platforms below or through mail.</span>
          </p>
        </div>
        <div className='contact-link'>
            <span className='contact-tagline'>I reply within 24 hrs at</span>
            <span><a
            href = 'mailto: husseinkzz@outlook.com'
            target = '_blank'
            rel = 'noreferrer'
            className = 'my-email'
            >
              husseinkizz@outlook.com
            </a></span>
            <span className= 'contact-icons-holder'>
              
          <span>
          <a 
          href= 'https://codepen.io/husseinkizz' 
          target= '_blank'
          rel = 'noreferrer'   
          className= 'icon-links'>
          <RiCodepenLine 
          className='contact-icons faa-spin animated-hover'/>
          </a>
          </span>

          <span>
          <a 
          href= 'https://www.linkedin.com/in/husseinkizz' 
          target= '_blank'
          rel = 'noreferrer'   
          className= 'icon-links'>
          <RiLinkedinBoxLine 
          className='contact-icons faa-vertical animated-hover'/>
          </a>
          </span>

          <span>
          <a 
          href= 'https://twitter.com/HusseinKizz' 
          target= '_blank'
          rel = 'noreferrer'   
          className= 'icon-links'>
          <RiTwitterLine 
          className='contact-icons faa-wrench animated-hover'/>
          </a>
          </span>

          <span>
          <a 
          href= 'https://github.com/Hussseinkizz' 
          target= '_blank'
          rel = 'noreferrer'   
          className= 'icon-links'>
          <RiGithubLine 
          className='contact-icons faa-shake animated-hover'/>
          </a>
          </span>

            </span>
          </div>
      </>
    )
  }
}
