import React, { Component } from 'react'
import logo from '../assets/logo-line-2.svg'
import {RiTwitterLine} from 'react-icons/ri'
import {RiGithubLine} from 'react-icons/ri'
import {RiLinkedinBoxLine} from 'react-icons/ri'
import {RiCodepenLine} from 'react-icons/ri'


export default class Nav extends Component {
  render() {
    return (
      <>
      <nav>
        <div className='nav-left'>

          <span>
          <a 
          href= 'https://codepen.io/husseinkizz' 
          target= '_blank'
          rel = 'noreferrer'  
          className= 'nav-left-links'>
          <RiCodepenLine 
          className='nav-icons nav-icons-a'/>
          </a>
          </span>

          <span>
          <a 
          href= 'https://www.linkedin.com/in/husseinkizz' 
          target= '_blank'
          rel = 'noreferrer' 
          className= 'nav-left-links'>
          <RiLinkedinBoxLine 
          className='nav-icons nav-icons-b'/>
          </a>
          </span>

          <span>
          <a 
          href= 'https://twitter.com/HusseinKizz' 
          target= '_blank'
          rel = 'noreferrer' 
          className= 'nav-left-links'>
          <RiTwitterLine 
          className='nav-icons nav-icons-c'/>
          </a>
          </span>

          <span>
          <a 
          href= 'https://github.com/Hussseinkizz' 
          target= '_blank'
          rel = 'noreferrer' 
          className= 'nav-left-links'>
          <RiGithubLine 
          className='nav-icons nav-icons-d'/>
          </a>
          </span>

        </div>

        <div className='nav-middle'>

        <span className='nav-middle-before'></span>

        <span className='brand'>

        <span>
        <a 
        href= 'https://husseinkizz.netlify.app' 
        target= '_blank' 
        rel = 'noreferrer'
        className= 'nav-middle-links nav-middle-links-a'>
        <span>
        <img 
        src= {logo} 
        alt= '' 
        className= 'logo nav-middle-links-b'/>
        </span>
        <span>Hussein Kizz</span>
        </a>
        </span>

        </span>

        <span className='nav-middle-after'></span>

        </div>


        <div className='nav-right'>

        <span className='hidden'>
          <a 
          href= '#about-me' 
          className= 'nav-right-links nav-right-links-a'>
          About
          </a>
          </span>

        <span className='hidden'>
          <a 
          href= '#my-projects' 
          className= 'nav-right-links nav-right-links-b'>
          Projects
          </a>
          </span>

        <span className='hidden'>
          <a 
          href= '#my-blog' 
          className= 'nav-right-links nav-right-links-c'>
          Blog
          </a>
          </span>

        <span className='hidden'>
          <a 
          href= '#contact-me' 
          className= 'nav-right-links nav-right-links-d'>
          Contact
          </a>
          </span>

        <span className='nav-action'>
          <a 
          href= 'https://bit.ly/hussein-resume' 
          target= '_blank'
          rel= 'noreferrer'
          id='resume-link' 
          className= 'nav-right-links'>
          Resume
          </a>
          </span>

        </div>
        
      </nav>
      </>
    )
  }
}
