import React, { Component } from 'react'
import {FaLaptopCode} from 'react-icons/fa'

export default class HeroSection extends Component {
  render() {
    return (
      <>
      <div className='hero'>
      <div className='hero-section'>
      <span className='hero-greeting'>Hi There?</span>
      <span className='hero-name'>It's Hussein Kizz</span>
      <span className='hero-title'>Building things for the web</span>
      <span className='hero-text'>And currently available for a web developer role in an exciting company and can also deliver cool authentic designs to all types of clients on a given schedule. Interested?</span>
      <a
      href = 'mailto:husseinkizz@outlook.com'
      target = '_blank'
      rel = 'noreferrer'
      className='hero-action'>
      Let's Talk
      </a>
      </div>
      <div className='hero-media'>
        <FaLaptopCode className='hero-image faa-float animated faa-slow'/>
      </div>
      </div>
      </>
    )
  }
}
